//
import React, { useState } from 'react';
import { supabase } from '../services/supabase_api';
import { validateEmail, validatePassword } from '../utils/validation';
import { useNavigate, Link as ReactRouterLink } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { updateSession } from '../Actions';
import { fetchMember } from '../services/memberService';
import {
  Box,
  Button,
  Container,
  Text,
  FormControl,
  FormLabel,
  Heading,
  Input,
  Stack,
  Divider,
  Link as ChakraLink,
  Center,
} from '@chakra-ui/react'


const homeUrl = process.env.PUBLIC_URL;

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');

  const dispatch = useDispatch()
  const navigate = useNavigate()


  const handleSubmit = async (event) => {
    event.preventDefault(); //

    const { data, error } = await supabase.auth.signInWithPassword({
      email: email.trim(),
      password: password,
    });
    if (error) {
      setMessage('');
    }else{
      const user_id = data.user.id;
      const member = await fetchMember(user_id)
      dispatch(updateSession(member));
      navigate(`${homeUrl}/`)
    }
  };

  // ()
  const validate = () => {
    return validateEmail(email) && validatePassword(password);
  };


  return (
    < Container maxW="lg" py={{ base: '12', md: '24' }} px={{ base: '0', sm: '8' }}>
      <Stack spacing="8">
      <Box
        py={{ base: '0', sm: '8' }}
        px={{ base: '4', sm: '10' }}
        bg={{ base: 'transparent', sm: 'bg.surface' }}
        boxShadow={{ base: 'none', sm: 'md' }}
        borderRadius={{ base: 'none', sm: 'xl' }}
      >
          <Stack spacing="6">
            <Heading mb={4}></Heading>
            <form onSubmit={handleSubmit}>
              <FormControl id="email" mb={4}>
                <FormLabel></FormLabel>
                <Input
                  type="email"
                  placeholder=""
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </FormControl>

              <FormControl id="password" mb={4}>
                <FormLabel></FormLabel>
                <Input
                  type="password"
                  placeholder=""
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </FormControl>
              <Stack spacing="6">
                <Button colorScheme="blue" type="submit" disabled={!validate()}></Button>
                <Divider borderColor={'black'}/>
                <Text>
                  
                </Text>
                <Center>
                  <ChakraLink as={ReactRouterLink} to={`${homeUrl}/userregistration`} color={'blue'}>
                    
                  </ChakraLink>
                </Center>
              </Stack>
            </form>
          </Stack>
        </Box>
      </Stack>
    </Container>
  );
};

export default Login;

